.container h4{
    letter-spacing: 2px;
    color: #000;
    background-color: transparent;
}
#cal {
    width: 400px;
    font-weight: 800;
    text-decoration: none;
    resize: both;
    display: flex;
    justify-content: center;
  }
  #cal div{
    background: #fff;
  }
  #cal button{
    box-shadow: none;
  }
  .container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 670px;
    background: none;
    position: absolute;
    top: 60px;
    left: 550px;
    box-shadow: 0px 0px 20px rgba(0,0,0,0.6);
    font-family: 'Poppins';
  }
  .container form button{
    width: 10px;
    height: 40px;
    border: none;
    outline: none;
    background: none;
  }
  .container button:hover{
    transition: 0.3s;
    background-color: silver;
    font-weight: 400;
    letter-spacing: .3px;
  }
  .container form button{
    font-size: 16px;
    width: 100px;
    height: 40px;
    margin-top: 10px;
    margin-left: -10px;
    border-radius: 20px;
    outline: none;
    border: 0.1px solid;
  }
  .container #mail{
    font-size: 16px;
    width: 380px;
    height: 40px;
    border: 1px solid;
    text-align: center;
    font-weight: 600;
  }
  #setTime{
    position: relative;
    top: 1px;
    left: 150px;
  }
  .container form button:hover{
    transition: 0.3s;
    background-color: silver;
    font-weight: 400;
    letter-spacing: .2px;
  }
  .timing{
    margin-top: 20px;
    margin-left: 10px;
    width: 380px;
    height: 400px;
   display: flex;
   align-items: center;
  }
  #slots{
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  #slots div { 
    background:none;
    border-radius: 50% !important;
  }
  .timing #slots input{
    border: none;
    outline: none;
    background: none;
    height: 100px;
    width: 100px;
    box-shadow: none;
    margin-left: 10px;
  }
  .timing p{
    height: 30px;
    width: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
    color: #000;
    position: relative;
    left: 0px;
  }
  #submit_btn
  {
    box-sizing: border-box;
    position: relative;
    height: 70px;
    width: 400px;
    box-shadow: 2px 2px 20px rgba(0,0,0,0.6) !important;
      font-size: 20px;
      text-transform: uppercase;
      cursor: pointer;
      padding: 5px;
  }
  #admin{
    display: none;
  }

