#home{
    width: 100%;
    height: 100vh;
    background:url(https://static.gobumpr.com/web-app/assets/bike-service-mob.jpg) no-repeat;
    background-position: center;
    margin-top: 5px;
}
#ServiceForm{
    height: 350px;
    width: 350px;
    background-color: #fff;
    box-shadow: 2px 2px 20px rgba(0,0,0,0.5);
    position: relative;
    top: 265px;
    left: 100px;
    text-align: center;
    box-sizing: border-box;
}
#ServiceForm h3{
    letter-spacing: 1.2px;
    font-size: 22px;
    color: rgba(0,0,0,0.8);
}
#ServiceForm h3 span {
    font-size: 24;
    font-weight: 800;
    letter-spacing: 2px;
    color: rgba(223, 10, 10, 0.9);
    text-shadow: 2px 2px 1px rgba(0,0,0,0.2);
    position: relative;
    top: 10px;
}
#vehicals , input, button{
    box-sizing: border-box;
    position: relative;
    top: 30px;
    height: 40px;
    width: 300px;
    border: none;
    outline: none;
    box-shadow: 5px 5px 20px rgba(0,0,0,0.5);
    font-size: 17px;
    cursor: pointer;
}
#ServiceForm input{
    margin-left: 25px;
    padding: 5px 5px 2px 20px;
    width: 300px;
}
#ServiceForm select {
    color: rgba(0,0,0,0.8);
    border: none;
    background : none;
    text-transform: uppercase;
}
#ServiceForm select option{
    text-shadow: 2px 2px 5px rgba(0,0,0,0.8);
    padding: 5px 5px 2px 20px;
}
.fa-mouse-pointer{
    position: relative;
    left: 297px;top: 30.2px;
    z-index: 100;
    font-size: 20px;
    color: #000;
}
#ServiceForm .fa-envelope{
    position: relative;
    top: 29px;
    left: -20px;
    color: #000;
}
#bookBtn , #check-price-btn{
    top: 90px;
    background: none;
    border: 1px solid;
}
.social-links{
    display: flex;
    flex-direction: column;
    height: 250px;
    width: 60px;
    position: relative;
    left: 1183px;top: -330px;
}
.social-links a {
    margin: 5px 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 2px rgba(248, 245, 245, 0.5);
    text-decoration: none;
    color: #fff;
  }
  .social-links a i{
    font-size: 20px;
  }
  .social-links a:hover{
      color: #fff;
      text-decoration: none;
  }
  .social-links a:nth-child(1) {
      background: linear-gradient(0deg, #000000, #252525)
  }
  .social-links a:nth-child(2) {
      background: linear-gradient(0deg, #0e9cf5, #41d6ff);
  }
  .social-links a:nth-child(3) {
    background: linear-gradient(0deg, #3C5A99, #577ac7);
  }
  .social-links a:nth-child(4) {
    background: linear-gradient(45deg, #405de6, #833ab4,  #fd1d1d);
  }
  .social-links a:nth-child(5) {
    background: linear-gradient(0deg, #006192, #019deb);
  }
#bookBtn{
    background : linear-gradient(rgba(255,0,0,0.9),yellow);
    position: absolute;
    top: -98px;
    right: -60px;
    width: 130px;
    height: 50px;
    border: none;
    box-shadow: 2px 2px 10px rgba(0,0,0,0.5);
    font-size: 18px;
    font-weight: 500;
    letter-spacing: .4px;
    border-radius: 7px;
}
#bookBtn:hover{
    transition: .5s;
    font-size: 17.4px;
}
#Map{
    height: 350px;
    width: 650px;
    background-color: #fff;
    box-shadow: 2px 2px 20px rgba(0,0,0,0.5);
    position: relative;
    top: -84px;
    left: 470px;
    text-align: center;
    box-sizing: border-box;
    opacity: 0.8;
    background: rgba(0,0,0,0.5);
}
#Check_Price{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #000;
}
#Check_Price.AddPrice{
    display: none;
}
#cal{
    display: flex;
}
#cal.showCal{
    display: none;
}

@media (prefers-color-scheme : light){
    body{
      color: #000;
      background:#fff;
    }
  }
  
