#nav {
  color: #fff;
  display: grid;
  grid-template-columns: 45% 55%;
  z-index: 10000;
}
#logo {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  left: -100px;
  cursor: pointer;
}
#logo select {
  background: none;
  border: none;
  outline: none;
  font-size: 17px;
  margin-left: 14px;
  margin-top: 5px;
  color: #fff;
}
#logo select option {
  color: #000;
}
#menu ul {
  display: flex;
  text-decoration: none;
  list-style: none;
}
#menu ul li {
  display: flex;
  margin-left: 40px;
  margin-top: 10px;
  font-size: 15px;
}
#menu ul li a {
  cursor: pointer;
  text-decoration: none;
  color: #fff;
}
#menu ul li a:focus {
  transition: 0.5s;
  border-bottom: 3px solid lime;
}
#menu ul li i {
  position: relative;
  top: 0.5px;
  right: 10px;
}
#toggle {
  position: relative;
  left: -20px;
  border: 1px solid;
  width: 56px;
  height: 24px;
  border-radius: 27px;
  background: rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  font-size: 13px;
}
.fa-cloud-sun {
  left: 6px;
  color: yellow;
  z-index: 100;
  opacity: 1;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
  font-size: 14;
}
.fa-cloud-moon {
  position: absolute;
  left: 15px;
  top: 5px;
  color: silver;
  z-index: 100;
  opacity: 1;
  text-shadow: 1px 1px 5px rgba(233, 227, 227, 0.8);
}
#toggle_button {
  background: #fff;
  width: 20.8px;
  border-radius: 50%;
  height: 20.8px;
  position: absolute;
  top: 1px;
  left: 6px;
  opacity: 1;
  z-index: 10000;
  cursor: pointer;
}
#toggle_button:focus {
  background: cyan;
  transition: 1s;
  left: 28px;
  opacity: 0.9;
}
.fa-bars,
.fa-times-circle {
  opacity: 0;
  position: absolute;
  left: -2000px;
}
