@media (prefers-color-scheme: light) {
  #nav {
    color: #000;
  }
  #menu ul li a {
    color: #000;
  }
  #logo select {
    color: #000;
  }
  #menu {
    background: #fff;
    color: black;
  }
}

@media screen and (max-width: 480px) {
  #nav {
    height: 50px;
  }
  #logo {
    left: 70px;
    font-size: 10px;
  }
  #logo #serviceText {
    display: none;
  }
  #logo select {
    font-size: 17px;
    margin-left: 24px;
  }
  #menu {
    display: flex;
    width: 200px;
    position: fixed;
    background: rgba(0, 0, 0, 1);
    color: #fff;
    right: 200px;
    left: 450px;
    top: 55px;
    z-index: 10000;
    line-height: 100px;
    transform: 0.5s;
    height: 100vh;
  }
  #menu.active {
    transition: 0.2s;
    left: 0;
  }
  #menu ul {
    flex-direction: column;
    color: #fff;
  }
  .fa-bars,
  .fa-times-circle {
    opacity: 1;
    position: relative;
    left: -240px;
    font-size: 17px;
    font-weight: 700;
  }
  .fa-times-circle {
    color: red;
    z-index: 1000;
    font-size: 24px;
    left: 170px;
    top: -24px;
    transition: 0.2s;
  }
  #toggle {
    display: none;
  }
  #menu ul li {
    position: relative;
    top: -120px;
  }
  #menu ul li a {
    color: lime;
  }
}

